@font-face {
  font-family: "Opensans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Opensans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1150px;
  padding-right: 30px;
  padding-left: 30px;
}
@media screen and (min-width: 768px) {
  .container {
    padding-right: 50px;
    padding-left: 50px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-family: "Opensans", "Arial", sans-serif;
  background-color: #ffffff;
  padding: 16px;
  color: #444444;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  border: none;
  box-shadow: inset 0 0 0 2px #444444;
}
@media screen and (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 20px;
  }
}
.button:hover {
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.page {
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  background-color: #ffffff;
}

.page-body {
  color: #000000;
  background-color: #ffffff;
  padding: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin: 0 auto;
  width: 100%;
}

.header-main {
  width: 100%;
  max-width: 1150px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.header-main__logo-wrapper {
  display: flex;
  justify-content: center;
  padding-top: 22px;
  padding-bottom: 22px;
}
@media screen and (min-width: 768px) {
  .header-main__logo-wrapper {
    position: absolute;
    left: 50px;
  }
}
@media screen and (min-width: 1150px) {
  .header-main__logo-wrapper {
    left: 50%;
    transform: translateX(-50%);
    padding-top: 9px;
    padding-bottom: 7px;
  }
}

.header-main__logo {
  position: relative;
  text-decoration: none;
  width: 86px;
  height: 35px;
  background-image: url("../img/svg/logo-mobile.svg");
  background-repeat: no-repeat;
  z-index: 5;
}
@media screen and (min-width: 768px) {
  .header-main__logo {
    width: 113px;
    height: 46px;
    top: 0;
    background-image: url("../img/svg/logo-tablet.svg");
  }
}
@media screen and (min-width: 1150px) {
  .header-main__logo {
    width: 149px;
    height: 72px;
    background-image: url("../img/svg/logo-desktop.svg");
  }
}
.header-main__logo:hover {
  opacity: 0.8;
}
.header-main__logo:active {
  opacity: 0.6;
}

.header-main__logo-image--not-mobile {
  padding-left: 6px;
}
@media screen and (min-width: 768px) {
  .header-main__logo-image--not-mobile {
    color: #444444;
    padding-left: 5px;
    width: 67px;
  }
}
@media screen and (min-width: 1150px) {
  .header-main__logo-image {
    width: 38px;
    height: 22px;
  }
  .header-main__logo-image--not-mobile {
    width: 78px;
    padding-left: 8px;
  }
}

.main-nav {
  display: flex;
  background-color: transparent;
}
@media screen and (min-width: 768px) {
  .main-nav {
    width: 100%;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #ffffff;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: transparent;
  }
  .main-nav__list--site-nav {
    grid-column: 2/3;
    grid-row: 1/2;
    grid-template-columns: 84px 1fr;
    padding-right: 50px;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__list {
    background-color: transparent;
    max-width: 100%;
  }
  .main-nav__list--site-nav {
    grid-column: 3/4;
    grid-row: 1/2;
  }
}

@media screen and (min-width: 768px) {
  .main-nav__list--main-nav {
    padding-left: 50px;
    background-color: transparent;
  }
  .main-nav__list--main-nav .main-nav__link {
    padding: 19px 0 19px 0;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__list--main-nav {
    box-shadow: none;
    padding-left: 15px;
  }
  .main-nav__list--main-nav .main-nav__item {
    width: 220px;
    padding: 0 20px 0 35px;
    min-height: 88px;
  }
  .main-nav__list--main-nav .main-nav__item:nth-child(odd) {
    box-shadow: 1px 0 0 0 rgba(0, 0, 0, 0.1);
  }
}

.main-nav__list--site-nav .main-nav__link:hover .main-nav__icon-img {
  color: #63d1bb;
  opacity: 1;
}
.main-nav__list--site-nav .main-nav__link:hover .main-nav__link-text {
  opacity: 0.6;
}
.main-nav__list--site-nav .main-nav__link:active {
  opacity: 0.6;
}

.main-nav__list--customer-nav {
  display: none;
}
@media screen and (min-width: 1150px) {
  .main-nav__list--customer-nav {
    padding-left: 15px;
    display: grid;
  }
  .main-nav__list--customer-nav .main-nav__item {
    width: 220px;
    padding: 0 20px 0 35px;
  }
  .main-nav__list--customer-nav .main-nav__item .main-nav__link {
    padding-top: 19px;
    padding-bottom: 19px;
  }
}

.main-nav__item {
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media screen and (min-width: 768px) {
  .main-nav__item {
    box-shadow: none;
  }
  .main-nav__list--site-nav .main-nav__item {
    min-height: 88px;
  }
}

@media screen and (min-width: 768px) {
  .main-nav__item--search {
    width: 84px;
    box-shadow: 1px 0 0 rgba(0, 0, 0, 0.1), inset 1px 0 0 rgba(0, 0, 0, 0.1);
  }
  .main-nav__item--search .main-nav__link-text {
    display: none;
  }
  .main-nav__item--search .main-nav__link {
    width: 84px;
    display: flex;
    justify-content: center;
  }
}

.main-nav__link {
  display: flex;
  width: 100%;
  align-items: center;
  padding: 28px 0 27px 76px;
  color: #000000;
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  text-decoration: none;
}
@media screen and (min-width: 768px) {
  .main-nav__link {
    padding-left: 0;
    font-size: 15px;
  }
}
.main-nav__list--site-nav .main-nav__link {
  padding-left: 0;
}
@media screen and (min-width: 1150px) {
  .main-nav__list--main-nav .main-nav__link {
    font-size: 17px;
  }
}
.main-nav__list--main-nav .main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__list--main-nav .main-nav__link:active {
  opacity: 0.3;
}
.main-nav__list--customer-nav .main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__list--customer-nav .main-nav__link:active {
  opacity: 0.3;
}
.main-nav__link .main-nav__icon-img {
  opacity: 0.2;
}

@media screen and (min-width: 768px) {
  .main-nav__link-text {
    font-size: 17px;
  }
}

.main-nav__icon-img {
  width: 76px;
}
@media screen and (min-width: 768px) {
  .main-nav__icon-img {
    width: 82px;
  }
}

.main-nav__delivery {
  color: #444444;
  display: none;
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
}
@media screen and (min-width: 768px) {
  .main-nav__delivery {
    display: flex;
    justify-content: flex-end;
    padding-right: 50px;
    padding-top: 19px;
  }
}
@media screen and (min-width: 1150px) {
  .main-nav__delivery {
    grid-column: 2/4;
  }
}

.main-nav__wrapper {
  position: relative;
}

.main-nav__toggle {
  position: absolute;
  top: 29px;
  right: 30px;
  width: 20px;
  height: 20px;
  background-image: url("../img/svg/close-menu.svg");
  background-color: inherit;
  background-repeat: no-repeat;
  background-position: center;
  padding: 0;
  display: flex;
  border: none;
  cursor: pointer;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media screen and (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__toggle.main-nav__toggle--close {
  right: 31px;
  top: 32px;
  width: 20px;
  height: 14px;
  background-image: url("../img/svg/open-menu.svg");
}

.no-js .main-nav__toggle {
  display: none;
}

.pop-up-menu {
  display: none;
}
@media screen and (min-width: 768px) {
  .pop-up-menu {
    display: grid;
    grid-template-columns: 48% 52%;
    grid-auto-flow: dense;
    width: 100%;
  }
}
@media screen and (min-width: 1150px) {
  .pop-up-menu {
    grid-template-columns: 450px 1fr 400px;
  }
}
.pop-up-menu::before {
  content: "";
  width: 100%;
}
@media screen and (min-width: 768px) {
  .pop-up-menu::before {
    grid-column: 1/2;
    grid-row: span 1;
    padding-left: 50px;
  }
}
@media screen and (min-width: 1150px) {
  .pop-up-menu::before {
    grid-column: 2/3;
    grid-row: 1/2;
    padding-left: 0;
  }
}

.pop-up-menu--is-open {
  position: absolute;
  z-index: 10;
  display: flex;
  flex-direction: column;
  width: 100%;
  left: 0;
}
@media screen and (min-width: 768px) {
  .pop-up-menu--is-open {
    z-index: 0;
    position: static;
    display: grid;
  }
}

.no-js .pop-up-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .no-js .pop-up-menu {
    padding-top: 0;
    display: grid;
  }
}

.pop-up-menu__button-close {
  position: absolute;
  top: 17px;
  right: 20px;
  width: 22px;
  height: 22px;
  padding: 0;
  display: flex;
  border: none;
  background-color: transparent;
  background-image: url("../img/svg/icon-cross.svg");
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .pop-up-menu__button-close {
    display: none;
  }
}
.pop-up-menu__button-close:hover {
  opacity: 0.6;
}
.pop-up-menu__button-close:active {
  opacity: 0.3;
}

.promo {
  position: relative;
  padding-top: 72px;
  background-color: #fafafa;
  border-top: 1px solid #ececec;
  z-index: 0;
  background-image: url("../img/decoration-images/promo-back-mobile@1x.png");
  background-repeat: no-repeat;
  background-position: 50% -18px;
  background-size: contain;
}
@media screen and (min-width: 768px) {
  .promo {
    padding-top: 155px;
    margin-top: -62px;
    background-position: 50% -2px;
    background-image: url("../img/decoration-images/promo-back-tablet@1x.png");
  }
  .promo::after {
    position: absolute;
    z-index: 1;
    content: "";
    width: 100%;
    height: 24px;
    bottom: 0;
    left: -4px;
    background-image: url("../img/svg/waves-white.svg");
    background-repeat: repeat-x;
  }
}
@media screen and (min-width: 1150px) {
  .promo {
    padding-top: 157px;
    background-position: 50% -2px;
    background-size: 67%;
  }
  .promo::after {
    background-image: url("../img/svg/waves-white-desktop.svg");
    height: 34px;
    left: -34px;
  }
}
.promo .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
}
@media screen and (min-width: 768px) {
  .promo .container {
    padding-right: 50px;
    padding-left: 50px;
  }
}

.promo__title {
  font-family: "Opensans", "Arial", sans-serif;
  color: #000000;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  max-width: 260px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 120px;
}
@media screen and (min-width: 768px) {
  .promo__title {
    font-size: 43px;
    line-height: 50px;
    margin-bottom: 372px;
    max-width: 668px;
  }
}
@media screen and (min-width: 1150px) {
  .promo__title {
    font-size: 53px;
    line-height: 57px;
    margin-bottom: 355px;
    max-width: 700px;
  }
}

.promo__categories-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 100%;
  background-color: #63d1bb;
}
@media screen and (min-width: 768px) {
  .promo__categories-list {
    display: grid;
    grid-template-columns: 50% 50%;
  }
}

.promo__categories-item {
  position: relative;
}

.promo__categories-link {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 24px 36px 18px 30px;
  font-family: "Opensans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  text-decoration: none;
  background-color: #63d1bb;
  color: #149080;
  transition: all 0.3s ease;
}
@media screen and (min-width: 768px) {
  .promo__categories-link {
    font-size: 20px;
    line-height: 30px;
    padding: 25px 36px 43px 36px;
  }
}
@media screen and (min-width: 1150px) {
  .promo__categories-link {
    padding-bottom: 49px;
  }
}
.promo__categories-item--toys .promo__categories-link {
  padding-right: 27px;
  background-color: #56c3b2;
}
@media screen and (min-width: 768px) {
  .promo__categories-item--toys .promo__categories-link {
    padding-right: 33px;
  }
}
@media screen and (min-width: 1150px) {
  .promo__categories-item--toys .promo__categories-link {
    padding-right: 35px;
  }
}
.promo__categories-link .promo__categories-text {
  color: #ffffff;
  max-width: 130px;
}
@media screen and (min-width: 768px) {
  .promo__categories-link .promo__categories-text {
    max-width: 130px;
  }
}
@media screen and (min-width: 1150px) {
  .promo__categories-link .promo__categories-text {
    max-width: 130px;
  }
}
.promo__categories-link .promo__categories-img {
  opacity: 0.5;
  transform: translateY(4px);
}

.promo__categories-link:hover {
  transform: scaleY(1.05) translateY(-5px);
}
.promo__categories-link:hover .promo__categories-img {
  opacity: 0.7;
}

.promo__categories-link:active .promo__categories-img {
  opacity: 0.3;
}

.top-product {
  position: relative;
  padding-top: 31px;
  padding-bottom: 42px;
}
@media screen and (min-width: 768px) {
  .top-product {
    padding-top: 40px;
    padding-bottom: 69px;
  }
}
@media screen and (min-width: 1150px) {
  .top-product {
    padding-top: 115px;
    padding-bottom: 0;
  }
}
.top-product:before {
  position: absolute;
  content: "";
  width: 100%;
  height: 6px;
  background-image: url("../img/svg/waves-gray-mobile.svg");
  background-repeat: repeat-x;
  bottom: 0;
}
@media screen and (min-width: 768px) {
  .top-product:before {
    height: 17px;
    background-image: url("../img/svg/waves-gray.svg");
  }
}
@media screen and (min-width: 1150px) {
  .top-product:before {
    background-image: none;
  }
}

@media screen and (min-width: 1150px) {
  .top-product .container {
    padding-bottom: 90px;
    position: relative;
  }
  .top-product .container::before {
    position: absolute;
    content: "";
    width: calc(100% - 100px);
    height: 17px;
    background-image: url("../img/svg/waves-gray-desktop.svg");
    background-repeat: repeat-x;
    bottom: 0;
  }
}

.top-product__title-wrapper {
  margin-bottom: 17px;
}
@media screen and (min-width: 768px) {
  .top-product__title-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    margin-bottom: 38px;
  }
}
@media screen and (min-width: 1150px) {
  .top-product__title-wrapper {
    max-width: 670px;
    margin-left: 32px;
    margin-bottom: 36px;
    position: relative;
    z-index: 1;
    display: grid;
    grid-template-columns: 100px 1fr;
    column-gap: 43px;
  }
}

.top-product__title {
  font-family: "Opensans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  margin: 0 0 4px 0;
  color: #000000;
}
@media screen and (min-width: 768px) {
  .top-product__title {
    font-size: 43px;
    line-height: 50px;
    margin-bottom: 0;
  }
}
@media screen and (min-width: 1150px) {
  .top-product__title {
    font-size: 53px;
    line-height: 57px;
