.top-product__weeks-product-text {
  font-family: "Opensans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
  max-width: 170px;
  text-transform: uppercase;
  display: block;
