.top-product__info-text {
  font-family: "Opensans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  margin: 0 0 27px 0;
}
@media screen and (min-width: 768px) {
  .top-product__info-text {
    font-size: 17px;
    line-height: 30px;
    margin-bottom: 46px;
  }
}
@media screen and (min-width: 1150px) {
  .top-product__info-text {
    margin-bottom: 57px;
  }
}

.top-product__details {
  font-family: "Opensans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  margin: 0 0 28px 0;
  display: grid;
  row-gap: 6px;
}
@media screen and (min-width: 768px) {
  .top-product__details {
    margin-bottom: 58px;
  }
}
@media screen and (min-width: 1150px) {
  .top-product__details {
    row-gap: 4px;
    margin-bottom: 110px;
  }
}

.top-product__details-wrapper {
  display: grid;
  grid-template-columns: 97px 1fr;
  padding-bottom: 9px;
  border-bottom: 1px solid #ececec;
}
@media screen and (min-width: 1150px) {
  .top-product__details-wrapper {
    padding-bottom: 11px;
  }
}

.top-product__details-value {
  margin: 0;
}

.top-product__image {
  display: flex;
  width: 100%;
  object-fit: contain;
  margin-bottom: 26px;
}
@media screen and (min-width: 768px) {
  .top-product__image {
    position: absolute;
    width: 334px;
    height: 404px;
    bottom: 0;
    right: 0;
    margin-bottom: 0;
  }
}
@media screen and (min-width: 1150px) {
  .top-product__image {
    width: 525px;
    height: 636px;
    z-index: 0;
  }
}

.top-product__price-button-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.top-product__price {
  display: block;
  font-family: "Opensans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  margin-bottom: 24px;
}
@media screen and (min-width: 768px) {
  .top-product__price {
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 33px;
  }
}

.top-product__button {
